.about-container {

    display: flex;
    flex-wrap: wrap;
    background: var(--black-color);
    min-height: 100vh;
    position: relative;

    .box {
        display: flex;
        align-items: center;
        color: var(--white-color);
        width: 900px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;

        h2 {

            font-size: 2.6rem;
            font-weight: 300;
            line-height: 8cqw;

        }

        h3 {
            font-weight: 600;
            line-height: 100px;
            font-size: 1.5rem;
        }

        p {
            font-weight: 200;
            line-height: 25px;
            font-size: 1.2rem;
        }

        img {
            width: 400px;
        }
    }


}